/* Ensure full height and center content */
body,
html {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-position: center;
  overflow: hidden; /* Prevent scrollbars */
}

.container {
  text-align: center;
  width: 100%;
  padding: 20px;
}

/* Search section styling */
.search-section {
  width: 100%;
  max-width: 60%;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

/* Input styling */
.spotlight-input {
  width: 70%;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px; /* Improved font size */
}

/* Button styling */
.background-button {
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  font-size: 16px; /* Improved font size */
}

.background-button:hover {
  background-color: #0056b3;
}

/* Results styling */
.spotlight-results {
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #f9f9f9;
  width: 100%;
}

/* Individual result item styling */
.spotlight-result-item {
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid #ccc;
  font-size: 18px; /* Improved font size */
}

.spotlight-result-item:hover {
  background-color: #f0f0f0;
}

/* Quote display styling */
.quote-display {
  font-size: 28px; /* Adjust as needed */
  font-weight: bold;
  color: #333; /* Adjust as needed */
  max-width: 100%;
  padding: 20px;
}

/* Quote controls styling */
.quote-controls {
  margin-top: 20px;
  text-align: center;
}

.quote-controls label {
  margin-right: 10px;
  font-size: 16px;
  font-weight: bold;
}

.quote-controls input {
  margin: 5px;
}

#font-size {
  background: linear-gradient(
    45deg,
    #f8f9fa,
    #e9ecef
  ); /* Light gradient background */
  border: 2px solid #dee2e6; /* Border with light color */
  border-radius: 4px; /* Rounded corners */
  padding: 8px 12px; /* Padding */
  font-size: 16px; /* Font size */
  color: #495057; /* Text color */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Light shadow */
  transition: all 0.3s ease; /* Smooth transition */
  outline: none; /* Remove default focus outline */
}

/* Styles for font color input */
#font-color {
  width: 40px; /* Fixed width for color picker */
  height: 40px; /* Fixed height */
  border: 2px solid #dee2e6; /* Border with light color */
  border-radius: 50%; /* Rounded corners for circular color picker */
  padding: 0; /* Remove padding */
  cursor: pointer; /* Pointer cursor */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Light shadow */
  transition: all 0.3s ease; /* Smooth transition */
  outline: none; /* Remove default focus outline */
}

/* Focus and hover styles for font color input */
#font-color:hover,
#font-color:focus {
  border-color: #007bff; /* Blue border on hover/focus */
  box-shadow: 0 4px 8px rgba(0, 123, 255, 0.2); /* Shadow effect on hover/focus */
}

.download-button {
  background: linear-gradient(
    45deg,
    #ff6b6b,
    #f06595
  ); /* Gradient background */
  border: none; /* Remove default border */
  color: white; /* White text color */
  font-size: 16px; /* Font size */
  font-weight: bold; /* Bold text */
  padding: 12px 24px; /* Padding */
  border-radius: 8px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor on hover */
  transition: all 0.3s ease; /* Smooth transition for effects */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Shadow effect */
  text-transform: uppercase; /* Uppercase text */
}

.download-button:hover {
  background: linear-gradient(
    45deg,
    #f06595,
    #ff6b6b
  ); /* Reverse gradient on hover */
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3); /* Darker shadow on hover */
}

.download-button:focus {
  outline: none; /* Remove default focus outline */
  box-shadow: 0 0 0 3px rgba(255, 105, 135, 0.4); /* Focus shadow effect */
}

/* Optional: Import a Google Font for a better appearance */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap");

body {
  font-family: "Roboto", sans-serif; /* Apply Google Font */
}
